.delivery-options {
  background-color: #f7f7f7;
  padding: 20px;
  margin: 20px 0;
  border-radius: 8px;
}

.delivery-options h2 {
  color: #333;
  font-size: 1.5em;
  margin-bottom: 10px;
}

.delivery-options ul {
  list-style: none;
  padding: 0;
}

.delivery-options li {
  font-size: 1.2em;
  margin: 8px 0;
}
